html {
  scroll-behavior: smooth;
}
.navbar {
  background: linear-gradient(to right, #3a60b3, #233b6f);
}

.navbar-brand {
  font-weight: 600;
  color: #fff !important;
}

.navbar-light .navbar-nav .nav-link {
  font-weight: 600;
  text-transform: uppercase;
}
.nav-link {
  color: #fff !important;
}

.navbar-light .navbar-toggler {
  border-color: transparent;
}

.navbar-light.fixed {
  position: fixed;
  top: 0;
  z-index: 99;
  width: 100%;
}
#home {
  padding-top: 25x;
  color: #fff !important;
}
.carousel-item img {
  height: 625px;
}

.promo-title {
  font-size: 35px;
  font-weight: 600;
  margin-top: 100px;
}
.d-block w-100 {
  width: 100%;
  height: 400px;
}

.page {
  position: relative;
  overflow: hidden;
  margin-top: 10px;
}

.page .content {
  margin-top: 4rem;
  margin-bottom: 4rem;
}

.page .content.cover {
  margin-top: 8rem;
  margin-bottom: 8rem;
}

.page .border {
  width: 6rem;
  height: 2px;
  margin-left: auto;
  margin-right: auto;
  background: #f5b76a;
}

.page.overlay:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: '';
  background: rgba(0, 0, 0, 0.2);
}

.page.overlay .content {
  position: relative;
  z-index: 1;
}

#services {
  background: #f5f5f5;
  border-bottom: 1px solid #e7e7e7;
}

#services .service i {
  margin-top: 4rem;
}

#services .service:hover i {
  color: #f5b76a;
}

#about .content {
  margin-bottom: 0;
}

#about .teammate {
  margin-top: 4rem;
  margin-bottom: 4rem;
}

#about .teammate .profile-photo {
  overflow: hidden;
}

#about .teammate .profile-photo img {
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

#about .teammate:hover .profile-photo img,
#about .teammate:focus .profile-photo img {
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
  transform: scale(1.1);
}

#about .teammate .bio .border {
  height: 1px;
}

#about .teammate .bio ul {
  margin-bottom: 0;
}

#about .teammate .bio a {
  color: #666;
}

#about .teammate .bio a:hover,
#about .teammate .bio a:focus {
  color: #222;
}
#contact {
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

#contact a {
  display: block;
}

#contact i {
  margin: 4rem 0 1rem;
}

#footer {
  padding: 4rem 0;
  color: #ccc;
  text-align: center;
  background: linear-gradient(to right, #3a60b3, #233b6f);
}

#footer a,
#footer a:hover,
#footer a:focus {
  color: #fff;
}

#footer .social {
  margin-bottom: 1rem;
}

#footer .social li .facebook:hover,
#footer .social li .facebook:focus {
  color: #3b5998;
}

#footer .social li .twitter:hover,
#footer .social li .twitter:focus {
  color: #00aced;
}

#footer .social li .google-plus:hover,
#footer .social li .google-plus:focus {
  color: #d34836;
}

#footer .social li .dribbble:hover,
#footer .social li .dribbble:focus {
  color: #ea4c89;
}

#footer .copyright {
  margin-bottom: 0;
}
